.mess_Section{
    margin-top: 5px;
    border-radius: 20px;
    padding: 3px;
    display: grid;
    background: rgba(55,55,55,0.7);
    grid-template-columns: 1fr 1fr;
}
/*@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@*/
.mess-List_Mess{
    display: flex;
    flex-flow: column;
    max-height: 710px;
    padding: 10px;
}
.mess_List_Desc{
    display: flex;
    flex-flow: column;
}
.mess_List_List{
    border: black solid;
    max-height: 492px;
    overflow: auto;
    padding: 20px;
}
.mess_List_Form{
}
.text-area{
    width: 87%;
    height: 27px;
}
.send-mess{
    position: relative;
    top: 20px;
    width: 30px;
    height: 30px;
    margin: 10px;
}
.send-mess:hover{
    border: #555555 solid;
    margin: 8px;
}
/*@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@*/
.mess_List_Dia{
    display: flex;
    flex-flow: column;
    max-height: 710px;
    padding: 10px;
}
.mess_List_Dia_Panel{
    text-align: center;
    border-radius: 20px;
}
#Button-Message{
    background: rgba(92,26,14,0.7);
    border: #e67315 1px solid;
    border-radius: 20px;
    margin: 5px;
    cursor: default;
}
#Button-Message:hover{
    box-shadow: 0 0 0 2px #555555, 0 0 0 4px #555555;
}
.mess_List_Dia_List{
    border-radius: 20px;
    padding: 12px;

}
.List-item-Party-Friend{
    border-radius: 10px;
    margin: 3px;
    padding: 5px;
    display: flex;
}
/*@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@*/
#mess_img{
    max-width: 588px;
    max-height: 600px;
    margin: auto;
}
#ava-min{
    border-radius: 20px;
    width: 100px;
    height: 100px;
}
.MESSAGE_ITEM{
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
    border-radius: 20px;
    padding-left:10px;
    margin: 2px;


}
#USER{
    background: rgba(230, 115, 21, 0.5);
    width: 50%;
}
#PROFILE{
    background: rgba(130, 115, 21, 0.5);;
    width: 50%;
    position: relative;
    left: 50%;
}
